'use client';

import {
  createContext,
  ReactNode,
  useContext,
  useEffect,
  useState,
} from 'react';
import { Question, User } from '../types.d'; // Make sure Question is exported from your types

// Extend the User type to include progress and bookmarks
interface UserProfile extends User {
  completedQuestions: Question[]; // Now storing the full Question object
  bookmarkedQuestions: Question[];
}

interface AuthContextType {
  user: UserProfile | null;
  token: string | null;
  login: (userData: User, token: string) => void;
  logout: () => void;
  isAuthenticated: boolean;
  isLoading: boolean;
  toggleQuestionProgress: (questionId: string) => void;
  toggleQuestionBookmark: (questionId: string) => void;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const AuthProvider = ({ children }: { children: ReactNode }) => {
  const [user, setUser] = useState<UserProfile | null>(null);
  const [token, setToken] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  const fetchUserProfile = async (authToken: string) => {
    try {
      const response = await fetch(
        'http://localhost:5000/api/v1/user/profile',
        {
          headers: { Authorization: `Bearer ${authToken}` },
        }
      );
      if (!response.ok) throw new Error('Failed to fetch profile');
      const { data } = await response.json();
      setUser(data);
    } catch (error) {
      console.error(error);
      logout(); // If token is invalid, log out
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    const storedToken = localStorage.getItem('token');
    if (storedToken) {
      setToken(storedToken);
      fetchUserProfile(storedToken);
    } else {
      setIsLoading(false);
    }
  }, []);

  const login = (userData: User, token: string) => {
    localStorage.setItem('token', token);
    // We don't store the full user object anymore, just the basic info
    localStorage.setItem(
      'user',
      JSON.stringify({
        id: userData.id,
        name: userData.name,
        email: userData.email,
      })
    );
    setToken(token);
    // After login, fetch the full profile with progress/bookmarks
    fetchUserProfile(token);
  };

  const logout = () => {
    setUser(null);
    setToken(null);
    localStorage.removeItem('user');
    localStorage.removeItem('token');
  };

  // This is a generic function creator for toggling items in a list
  const createOptimisticToggle =
    (
      listName: 'completedQuestions' | 'bookmarkedQuestions',
      endpoint: 'progress' | 'bookmark'
    ) =>
    async (questionId: string) => {
      if (!token || !user) return;

      // Store the original user state in case we need to revert
      const originalUser = { ...user };

      const list = user[listName] || [];
      const isAlreadyInList = list.some((q) => q._id === questionId);

      // Optimistically update the UI right away for a snappy user experience
      const updatedList = isAlreadyInList
        ? list.filter((q) => q._id !== questionId)
        : [...list, { _id: questionId }]; // Note: This is a partial object for the optimistic update

      setUser({ ...user, [listName]: updatedList as Question[] });

      // Now, make the actual API call
      try {
        const response = await fetch(
          `http://localhost:5000/api/v1/user/${endpoint}`,
          {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
              Authorization: `Bearer ${token}`,
            },
            body: JSON.stringify({ questionId }),
          }
        );
        if (!response.ok) throw new Error(`Failed to update ${endpoint}`);

        // Re-fetch the full user profile to get the accurate, populated data from the backend
        await fetchUserProfile(token);
      } catch (error) {
        console.error(error);
        // If the API call fails, revert the UI to the original state
        setUser(originalUser);
      }
    };

  const toggleQuestionProgress = createOptimisticToggle(
    'completedQuestions',
    'progress'
  );
  const toggleQuestionBookmark = createOptimisticToggle(
    'bookmarkedQuestions',
    'bookmark'
  );

  return (
    <AuthContext.Provider
      value={{
        user,
        token,
        login,
        logout,
        isAuthenticated: !!token,
        isLoading,
        toggleQuestionProgress,
        toggleQuestionBookmark,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};
